.modal {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 500px;
  background-color: $light-blue;
  padding: 6px 33px 46px 38px;
  margin: 0 auto;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  transition: 1s;
  z-index: 100;

  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.modal__index {
  top: -1000px;
}

.modal__modal {
  top: 146px;
}


.modal__close {
  background-color: transparent;
  border: none;
  align-self: flex-end;
  padding: 10px 10px;
  margin: 4px -17px 0 0;
}

.modal__close:hover,
.modal__close:focus,
.modal__close:active  {
  border: 1px solid $white;
  transition: 1s;
}

.modal h2 {
  width: 100%;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 140.62%;
  color: $white;
  padding: 0;
  margin: -14px 0 7px;
}

.modal p {
  width: 100%;
  font-family: "PT Mono", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $white;
  padding: 0;
  margin: 0 0 35px 0;
}

.modal input {
  width: 99%;
}

.modal textarea {
  width: 99%;
  margin: 0 0 19px 0;
}

.modal__button {
  width: 99%;
  background-color: $main-blue;
  margin: 0 auto 18px;
}

.modal label {
  width: 99%;
  padding: 0;
  margin: 0 0 0 9px;
}
